@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.3/font/bootstrap-icons.css");

.app {
    position: relative;
    min-height: 92vh;
}

.skills {
  margin-left: 20%;
}

@media (max-width:1200px) {
  .app {
    min-height: 90.0vh;
  }

}

@media (max-width: 800px) {
  .skills {
    margin-left: 0%;
  }
  .app {
    min-height: 80vh;
  }

}

@media (max-width: 600px) {
  .skills {
    margin-left: 0%;
  }
  .app {
    min-height: 67vh;
  }

}

