.home{
    position: relative;
}

.text-home{
    position: relative;
    top: 10vh;
    margin-right: 5%;
    margin-left: 5%;
    font-size: 160%;
}

@media (max-width:1600px) {
    .text-home {
    padding-left: 3%;
    padding-right: 3%;
    font-size: 120%;
    }
}


@media (max-width:1200px) {
    .text-home {
    top: 5vh;
    padding-left:  1%;
    padding-right:  1%;
    font-size: 100%;
    }
}

@media (max-width: 800px) {
    .text-home {
    left: auto;
    right: auto;
    top: 1vh;
    padding-left: 1%;
    padding-right: 1%;
    font-size: 100%;
    }

}
  